import React, {useState} from 'react'

export default function Booth(args) {
    const [option, count] = useState('args.votes')
    const options = args.options.map((option, index) => (
        <button
          key={option}
        >
          {option}
        </button>))
    return (
        <div>
            {options}
        </div>
    )
}
